*{
    padding: 0;
    margin: 0;
}
body{
    font-family: var(--font_1);
    padding-top: 64px;
}
.container {
    max-width: 1032px;
    width: 90%;
    margin: 0 auto;
    color: var(--color_1);
}
.text_center {
    text-align: center;
}
.hide {
    display: none!important;
}
img, iframe {
    max-width: 100%;
}
.section_padding {
    padding: 30px 0;
}

